    text-transform: initial;
    @include r($lg) {
      text-transform: uppercase; } } }

.title-medium {
  @include notosans;
  font-size: 3.6rem;
  font-weight: 700;
  line-height: 1.36;
  letter-spacing: -0.001em;
  text-align: center;
  &_blue {
    color: $ocean-blue; }
  &_white {
    color: $white; }
  &_regular {
    font-weight: 400; }
  @include r($sm) {
    font-size: 2.8rem; } }

.title-request {
  @include roboto;
  font-size: 4.8rem;
  text-align: center;
  font-weight: 400;
  line-height: 1.32;
  color: $white;
  @include r($md) {
    br {
      display: none; } }
  @include r($mob) {
    font-weight: 700; } }

.title-small {
  @include notosans;
  font-size: 2.8rem;
  font-weight: 700;
  @include r($sm) {
    font-size: 2.1rem; }
  &_minHeight {
    min-height: 8rem;
    line-height: 8rem;
    span {
      line-height: 1.3;
      vertical-align: middle;
      display: inline-block; } } }

.title-little {
  @include notosans;
  font-size: 2.4rem;
  font-weight: 700;
  text-align: center;
  &_blue {
    color: $ocean-blue; }
  &_white {
    color: $white; } }

.title-sm {
  font-size: 2.1rem;
  font-weight: 700;
  @include r($lg) {
    font-size: 1.7rem; } }


.subtitle {
  margin: 0 0 1.8rem;
  color: #1a6292;
  font-size: 4.2rem;
  line-height: 1.1;
  text-align: center;
  @include r($xlg) {
    font-size: 3.6rem; }
  @include r($md) {
    font-size: 2.8rem; }
  @include r($sm) {
    font-size: 1.8rem; } }

.description {
  font-size: 2.1rem;
  text-align: center; }

.inline-group {
  margin-bottom: 3.5rem;
  display: flex;
  justify-content: center;
  .subtitle {
    padding-left: 1rem; } }

.btn {
  display: inline-block;
  border: 1px solid;
  padding: 0 2rem;
  height: 4.4rem;
  line-height: 4.4rem;
  min-width: 26rem;
  border-radius: 4.4rem;
  letter-spacing: 2px;
  font-weight: 900;
  font-size: 2.4rem;
  color: $ocean-blue;
  @include roboto;
  text-align: center;
  background: none;
  text-transform: uppercase;
  transition: background, color, .3s;
  @include r($md) {
    font-size: 2.1rem; }
  &:hover {
    background: $ocean-blue;
    color: #fff;
    border-color: rgba(#fff, 0);
    .icon-load {
      fill: white; } }
  &_white {
    color: #fff;
    &:hover {
      background: #fff;
      color: $ocean-blue; } }
  &_small {
    font-size: 1.8rem; }
  &_xs {
    min-width: 18rem;
    height: 3rem;
    font-size: 1.2rem;
    font-weight: 400;
    line-height: 3rem; }
  &_more {
    font-size: 1.9rem;
    height: 4rem;
    min-width: 16.4rem;
    line-height: 4rem;
    color: $sky-blue;
    &:hover {
      background: $sky-blue; } }
  .icon-load {
    margin: -.4rem 1rem 0 0;
    font-size: 1.8rem;
    fill: $ocean-blue;
    transition: fill .2s; } }

$colorName: "yellow", "green", "red", "blue", "blue3", "black", "black2";
$color: $yellow, $dark-mint, $coral, $ocean-blue, $sky-blue, $dark, $cool-grey;
$colorHover: $light-gold, $light-teal, $pale-pink, $light-navy, $sky-blue, $cool-grey, $cool-grey;

@for $i from 1 through length($colorName) {
  .btn_full-#{nth($colorName, $i)} {
    border: 0;
    color: #fff;
    background: nth($color, $i);
    &:hover {
      background: nth($colorHover, $i); }
    &:active {
      background: darken(nth($colorHover, $i), 10%); } } }

@for $i from 1 through length($colorName) {
  .btn_stroke-#{nth($colorName, $i)} {
    border-color: nth($color, $i);
    color: nth($color, $i);
    background: transparent;
    &:hover {
      background: nth($colorHover, $i); }
    &:active {
      background: darken(nth($colorHover, $i), 10%); } } }

.btn-plain {
  display: inline-block;
  border: 1px solid;
  padding: 0 .5rem;
  height: 4.4rem;
  line-height: 4.4rem;
  min-width: 26rem;
  border-radius: 4.4rem;
  letter-spacing: 2px;
  font-weight: 900;
  font-size: 2.4rem;
  color: #fff;
  @include roboto;
  text-align: center;
  background: none;
  text-transform: uppercase;
  &:hover {
    background: #fff;
    color: $dark; } }

.btn-action {
  display: inline-block;
  padding: 0 1rem;
  min-width: 16.3rem;
  height: 4.4rem;
  line-height: 4.4rem;
  border-radius: 10rem;
  background-color: $yellow;
  transition: color .2s ease-in-out, background-color .2s ease-in-out, box-shadow .2s ease-in-out, border-color .2s ease-in-out;
  box-shadow: 0 1px 4px #8E6E09;
  color: #f4f4f4;
  font-size: 2.4rem;
  font-weight: 900;
  @include roboto;
  text-align: center;
  letter-spacing: 0.2rem;
  text-transform: uppercase;
  &:hover {
    background-color: $light-gold; }
  &:active {
    box-shadow: none; }
  &_white {
    color: $ocean-blue;
    box-shadow: 0 10px 30px 0 #08427c;
    background-color: $white;
    &:hover {
      color: $white;
      box-shadow: 0 5px 15px 0 #08427c;
      background-color: lighten($ocean-blue, 30); } }
  &_sm {
    font-size: 1.7rem; }
  .icon {
    margin-left: 0.3rem;
    font-size: 1.8rem;
    fill: currentColor; }
  .icon-send {
    margin-top: 1.3rem;
    vertical-align: top; } }

.field {
  @include r($lg) {
    margin-bottom: 2rem; }
  &__input {
    @include r($md) {
      padding: .8rem 1.5rem; }
    @include r($sm) {
      font-size: 1.6rem; }
    &:focus,
    &:valid,
    &.is-filled {
      ~ .field__name {
        @include r($lg) {
          top: -2.2rem;
          font-size: 1.5rem; }
        @include r($sm) {
          top: -1.2rem; } } } }
  &__name {
    @include r($lg) {
      font-size: 1.8rem; }
    @include r($sm) {
      font-size: 1.6rem; } }
  &_big {
    margin-bottom: 3rem;
    .field__input {
      border-bottom-width: 1px;
      padding: 1.2rem 1.5rem;
      @include r($md) {
        padding: .7rem 1.5rem;
        font-size: 2.1rem; }
      @include r($sm) {
        padding: .8rem 1.5rem; } } } }
.section_talk {
  .field__input,
  .hs-input {
    &:-webkit-autofill {
      -webkit-box-shadow: inset 0 0 0 1000px #fff !important;
      -webkit-text-fill-color: #000 !important;
      color: #000 !important; } } }
.section_blue {
  .field__input,
  .hs-input {
    &:-webkit-autofill {
      -webkit-box-shadow: inset 0 0 0 1000px #0855a2 !important;
      -webkit-text-fill-color: #fff !important;
      color: #fff !important; } } }
.field-info {
  margin-top: 5rem;
  font-size: 2.1rem;
  a {
    font-weight: 700;
    border-bottom: 1px solid transparent;
    transition: border-color .3s ease-in-out;
    color: inherit;
    &:hover {
      border-color: currentColor; } } }
.field-action {
  margin: 3rem 0 0;
  text-align: center; }

.field-file {
  position: relative;
  .file {
    position: absolute;
    right: 0;
    bottom: 0; }
  .field__input {
    padding-right: 4rem; } }

.file {
  display: block;
  cursor: pointer;
  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 3rem;
    height: 4.2rem;
